import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const availableLangue = JSON.parse(languages)
const contextLanguages = JSON.parse(contextLanguage)
const translations = JSON.parse(translationList)
const imagePath = imgPath

export default new Vuex.Store({
    state: {
        languages: availableLangue,
        fieldLanguage: contextLanguages.iso_code.toString(),
        translations: translations,
        imagePath: imagePath
    },
    mutations: {
        updateFieldLanguage (state, payload) {
            state.fieldLanguage = payload
        }
    },
    created () {
        console.log('ADRIEN MA PETITE BIATCH')
    }
})
